import {Component, Input, OnInit} from '@angular/core';
import {Playlist, PlaylistService} from '../../../services/api/playlist.service';
import {AlertController, PopoverController} from '@ionic/angular';
import {FileService, File} from '../../../services/api/file.service';
import {PlayerService} from '../../../services/player.service';

@Component({
    templateUrl: './more.component.html',
    styleUrls: ['./more.component.scss'],
})
export class MoreComponent implements OnInit {
    @Input() file: File;
    @Input() playlist: Playlist;

    constructor(private fileService: FileService,
                private popoverController: PopoverController,
                private playlistService: PlaylistService,
                private playerService: PlayerService,
                private alertController: AlertController) {
    }

    ngOnInit() {
    }

    remove() {
        this.popoverController.dismiss().then(async () => {
            const alert = await this.alertController.create({
                header: 'Warning',
                message: 'Do you really want to delete this song from the playlist?',
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Delete',
                        handler: () => {
                            this.playlist.detach(this.file);
                        }
                    }
                ]
            });
            await alert.present();
        });
    }
}
