import {Component, Input, OnInit} from '@angular/core';
import {AlertController, ModalController, NavController, PopoverController} from '@ionic/angular';
import {Playlist, PlaylistService} from '../../../services/api/playlist.service';
import {FilesComponent} from "../../../components/files/files.component";

@Component({
    templateUrl: './actions.component.html',
    styleUrls: ['./actions.component.scss'],
})
export class ActionsComponent implements OnInit {
    @Input() playlist: Playlist;

    constructor(private playlistService: PlaylistService,
                private popoverController: PopoverController,
                private alertController: AlertController,
                private navController: NavController,
                private modalController: ModalController) {
    }

    ngOnInit() {
    }


    async rename() {
        this.popoverController.dismiss().then(async () => {
            const alert = await this.alertController.create({
                header: 'Rename Playlist',
                inputs: [
                    {
                        name: 'name',
                        type: 'text',
                        placeholder: 'Enter Playlist Name...',
                        value: this.playlist.name
                    }
                ],
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Rename',
                        handler: (data) => {
                            this.playlist.update({name: data.name});
                        }
                    }
                ]
            });

            return await alert.present();
        });
    }


    async addFiles() {
        this.popoverController.dismiss().then(async () => {
            const modal = await this.modalController.create({
                component: FilesComponent,
                componentProps: {
                    playlist: this.playlist
                },
                animated: true,
                backdropDismiss: false,
                keyboardClose: false
            });
            return await modal.present();
        });
    }

    reorder() {
        this.popoverController.dismiss({reorder: true});
    }

    async remove() {
        this.popoverController.dismiss().then(async () => {
            const alert = await this.alertController.create({
                header: 'Warning',
                message: 'Do you really want to delete this playlist?',
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Delete',
                        handler: () => {
                            this.playlist.delete().then(() => {
                                this.navController.navigateBack('/');
                            });
                        }
                    }
                ]
            });
            await alert.present();
        });

    }
}
