import {Component, Input, ViewChild} from '@angular/core';
import {IonReorderGroup} from '@ionic/angular';
import {Subscription} from "rxjs";

@Component({
    selector: 'app-reorder-list',
    templateUrl: './reorder-list.component.html',
    styleUrls: ['./reorder-list.component.scss'],
})
export class ReorderListComponent {
    @ViewChild('reorderGroup', { static: true }) reorderGroup: IonReorderGroup;
    @Input('disabled') disabled = true;

    private subscription: Subscription;

    constructor() {
    }

    bind(collection) {
        this.subscription = this.reorderGroup.ionItemReorder.subscribe(e => {
            collection.splice(e.detail.to, 0, collection.splice(e.detail.from, 1)[0]);
            e.detail.complete(true);
        });
    }

    unbind() {
        this.subscription.unsubscribe();
    }

}
