import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {ModalController, PopoverController} from '@ionic/angular';
import {ActivatedRoute} from '@angular/router';
import {ActionsComponent} from './actions/actions.component';
import {ReorderListComponent} from '../../components/reorder-list/reorder-list.component';
import {Playlist, PlaylistService} from '../../services/api/playlist.service';
import {File, FileService} from '../../services/api/file.service';
import {PlayerService} from '../../services/player.service';
import {MoreComponent} from './more/more.component';
import {PreviewComponent} from "../../components/preview/preview.component";
import {Order} from "../../providers/api/resource";

@Component({
    selector: 'app-playlist',
    templateUrl: './playlist.page.html',
    styleUrls: ['./playlist.page.scss'],
})
export class PlaylistPage implements OnInit, AfterViewInit {
    public playlist: Playlist;
    public files: File[];
    public reorder = false;

    @ViewChild('reorderList', { static: true }) reorderList: ReorderListComponent;

    constructor(private playlistService: PlaylistService,
                private fileService: FileService,
                private route: ActivatedRoute,
                private popoverController: PopoverController,
                private playerService: PlayerService,
                private modalController: ModalController) {
    }


    ngOnInit() {
        const playlistId = Number(this.route.snapshot.paramMap.get('id'));
        this.playlistService
            .with(this.fileService)
            .orderBy('files.position', Order.ASC)
            .find(playlistId)
            .then(playlist => {
                this.playlist = playlist;
            }, (e) => {
                this.playlist = null;
            });
    }

    ngAfterViewInit() {
    }

    async actions(ev: any) {
        const popover = await this.popoverController.create({
            component: ActionsComponent,
            componentProps: {
                playlist: this.playlist
            },
            event: ev,
            translucent: false,
            mode: 'ios',
        });
        await popover.present();

        const {data} = await popover.onDidDismiss();

        if (data && data.hasOwnProperty('reorder')) {
            this.reorder = true;

            console.log(this.playlist.files);

            this.reorderList.bind(this.playlist.files);
        }
    }


    async more(ev: any, file) {
        const popover = await this.popoverController.create({
            component: MoreComponent,
            componentProps: {
                file: file,
                playlist: this.playlist
            },
            event: ev,
            translucent: false,
            mode: 'ios',
        });
        await popover.present();
    }

    async preview(file) {
        const modal = await this.modalController.create({
            component: PreviewComponent,
            cssClass: 'preview-modal',
            componentProps: {
                'fileId': file.id
            }
        });
        modal.present();
    }

    saveOrder() {
        this.playlist.sync(this.fileService).then(() => {
            this.reorder = false;
            this.reorderList.unbind();
        });
    }
}
