import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from "@ionic/angular";
import {File, FileService} from "../../services/api/file.service";
import {Order, Resource} from "../../providers/api/resource";
import {Playlist} from "../../services/api/playlist.service";

@Component({
    selector: 'app-files',
    templateUrl: './files.component.html',
    styleUrls: ['./files.component.scss'],
})
export class FilesComponent implements OnInit {
    @Input() playlist: Playlist;

    public searchTerm: string;
    public files: File[] = [];

    private searcher: Resource<File>;


    constructor(
        private fileService: FileService,
        private modalController: ModalController
    ) {
    }

    async close() {
        setTimeout(() => {
            this.modalController.dismiss();
        }, 100);
    }

    ngOnInit() {
    }

    ionViewWillEnter() {
        this.searcher = this.fileService;
        if (this.searchTerm) {
            this.searcher.where({
                filename: {
                    operator: 'like',
                    value: '%' + this.searchTerm + '%'
                }
            });
        }
        this.searcher.orderBy('id', Order.DESC)
            .limit(100)
            .page(1)
            .subscribe(result => {
                this.files = result.map(file => {
                    file.selected = !!this.playlist.files.find(f => f.id === file.id);
                    return file;
                });
            });
    }

    ionViewWillLeave() {
        this.searcher.unsubscribe();
    }

    search() {
        this.searcher.search([
            {
                filename: {
                    operator: 'like',
                    value: '%' + this.searchTerm + '%'
                }
            }
        ]);
    }

    async add(file?) {
        this.playlist.attach(file).then(() => this.search());
    }

    async remove(file?) {
        this.playlist.detach(file).then(() => this.search());
    }

}
